<template>
  <div class="activity padding_30">
    <div class="flex padding_b_30 timeline relative" v-for="t in timelime">
      <div class="">
        <div class="font_18">{{ t.time }}</div>
        <div class="light text_right">{{ t.year }}</div>
      </div>
      <div>
        <div class="border circle flex_center bg_white" style="width:48px;height:48px;margin:0 20px">
          <img style="width:20px" :src="flag">
        </div>
      </div>
      <div class="flex_1">
        <div class="font_18 font_bold">{{ t.title }}</div>
        <div class="light">{{ t.text }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/api'
export default {
  name: 'activity',
  data () {
    return {
      flag: require('@/assets/images/detail/flag.png'),
      timelime: [{time: '4月16日', year: '2017', title: '项目开始进行ICO', text: '由 Genaro Network 发起的项目「Genaro Network」上线了。 此项目必须在2017年08月31日21:00 之前，达到 2000ETH 的目标才可成功。'}, {time: '4月10日', year: '2017', title: 'ico项目上线', text: ''}]
    }
  },
  created () {
    api.post('detail/project_news', {sign: 'token=0&project_id=1'}).then(res => {
      console.log(res.data)
      // this.project = res.data
    })
  }
}
</script>

<style type="text/css">
  .timeline:not(:last-child):after{
    content:"";
    position: absolute;
    top: 0;
    left: 109px;
    width: 1px;
    height: 100%;
    background: #dbdee5;
    z-index: -1
  }
</style>



